import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { SearchService } from '../contact/service/search.service';
import { Search } from '../contact/model/search';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css']
})
export class SearchComponent implements OnInit {

  constructor(private sarchService:SearchService) { 
    console.log("===============SearchComponent==========================");
  }

  ngOnInit() {
  }
  searchForm = new FormGroup({
    search: new FormControl('')
  });
 
  message =''
  results :Array<Search> =null;

  submit(event){  
    console.log(this.searchForm.value);
    console.log(this.searchForm.value.search);
    this.sarchService.search(this.searchForm.value.search).subscribe(response=>{
      console.log(response);
      if(response!=null){
        this.results= response;
      }else{
      this.message ="No result found";
      }
})
  }

}
