import { Component, OnInit, HostListener } from "@angular/core";

import { Validators, FormBuilder, FormGroup, FormArray, ValidationErrors } from "@angular/forms";
import { ContactService } from './service/contact.service';

@Component({
  selector: "app-contact",
  templateUrl: "./contact.component.html",
  styleUrls: ["./contact.component.css"],
})
export class ContactComponent implements OnInit {
 
  form: FormGroup;
  submited= false;
  success =null;  
  error=null

  constructor(private fb: FormBuilder, private contactService :ContactService) {
    
   }

  ngOnInit(): void {
    this.form = this.fb.group({
       contact: this.fb.array([]),
    });
    this.addContact();
  }

  
  onSubmit() {
   this.submited =true; 
   this.error=null  
   this.success=null 
    const contact = this.form.controls.contact as FormArray;
        if( contact.status ==="VALID"){        
         this.contactService.saveContact(contact.value).subscribe(
          res=>{       
            this.success= res;
          },
          error=>{           
            this.error = error.error;
          }
         );
        }
      
  }

  remove(index){   
    const contact = this.form.controls.contact as FormArray;
    contact.removeAt(index);
    this.success='';
    }

  addContact() {
    this.success='';
    this.submited= false;
    const contact = this.form.controls.contact as FormArray;
    contact.push(this.fb.group({
      name: ['', Validators.compose([Validators.required,  Validators.pattern('[a-zA-Z]*')])],
      email: ['', Validators.compose([Validators.required, Validators.pattern("^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$")])],      
      phone: ['', Validators.compose([Validators.required,  Validators.pattern('[0-9]{10}')])],

    }));
   
  }

  trackByFn(index: any, item: any) {
    return index;
 }
 
}
