import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Contact } from '../model/contact';
import { Observable } from 'rxjs';
@Injectable({
  providedIn: 'root',
})
export class ContactService {

  constructor(private _http: HttpClient) { }

    public saveContact(contacts:Contact[]){
      let httpOptions = { headers: new HttpHeaders({ 'Content-Type': 'application/json', }), responseType: 'text' as 'json' };
       return this._http.post("http://13.233.83.113:8080/NetpayApp/api/contact/save",contacts, httpOptions);
      }
}